body.active-modal {
    overflow-y: scroll;
    
}

.btnModal {
    padding: 10px 20px;
    display: block;
    margin: 100px auto 0;
    font-size: 18px;
}

.modal{
    z-index: 5;
}

.modal , .overlay{
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
}

.overlay {
    background: rgba(49,49,49,0.8);
}


.modalContent {
    position: absolute;
    margin-top: 110px;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    line-height: 1.4;
    background: #f1f1f1;
    padding: 14px 28px;
    border-radius: 3px;
    width: 40vw;
    /* min-width: 40vw; */
    height: 600px;
    color: black;
    overflow-y:scroll;
    overflow-x:hidden;
    background-color: black;
}

.closeModal {
    position: absolute;
    top: 0px;
    right: 10px;
    padding: 5px 7px;
}

.closeModal img{
    width: 50px;
    height: 50px;
}

@media(max-width: 768px){
    .modalContent{
        width: 80vw;
        overflow-x: scroll;
    }
}