.skillsSet{
    display: flex;
    align-items: center;
    margin-top: 50px;
    justify-content: center;
    flex-direction: column;
    padding: 20px;
    margin: 20px;
    /* z-index: 1; */
}

.title{
    display: flex;
    align-items: center;
    /* z-index: 1; */
}

.title img{
    height: 7.81vw;
    /* z-index: 1; */
}

.skillTitle{
    font-size: 6.5vw;
    padding-bottom: 4px;
    position: relative;
    text-decoration: none;
    /* z-index: 1; */
}

.skillTitle::after {
    /* background: #ff0000; */
    background: linear-gradient(to right, transparent 0%, #ff0000 50%, transparent 100%);  
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    height: 2px;
    width: 100%;
 }

.skillDisplay{
    display: flex;
    align-content: center;
    flex-wrap: wrap;
    /* z-index: 1; */
}

.skills{
    display: flex;
    padding: 20px;
    align-items: center;
    justify-content:space-evenly;
    flex-direction: column;
    position: relative;
    background-color: black;
    margin: 8px;
    border-radius: 10px;
    transition: 0.5s;
}

