/* .skills:hover::after {
    filter:blur(0.5rem);
    opacity: 1;
}  */
.skills:hover{
    transform: scale(1.1);
    border: 1px solid red;
}

.skills:hover img{
    transform: scale(1.2);
}

.skills:hover span{
    transform: scale(1.2);
}

.skills img{
    margin-bottom: 10px;
    width: 6.5vw;
}

@keyframes spin {
    from{
        --angle: 0deg;
    }
    to{
        --angle: 360deg;
    }
}
