.about{
    display: flex;
    align-items: center;
    /* margin-top: 50px; */
    justify-content: center;
    flex-direction: column;
    width: 100%;
    overflow: hidden;
    position: relative;
    background: linear-gradient(180deg, #111132, #505064);
}

.mountains{
    background-image: url("mountains.png");
    /* background-attachment: fixed; */
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 3;
}

.stars{
    background-image: url("stars.png");
    /* background-attachment: fixed; */
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 1;
}

.planets{
    background-image: url("sun.png");
    /* background-attachment: fixed; */
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 2;
}

.title{
    display: flex;
    align-items: center;
}

.title img{
    height: 9.7vw;
    z-index: 3;
}

.aboutTitle{
    font-size: max(6.5vw, 30px);
    padding-bottom: 4px;
    position: relative;
    text-decoration: none;
    /* z-index: 1; */
    width: 100%;
    z-index: 3;
}

.icon{
    width: 50px;
    z-index: 3;
}

.aboutTitle::after {
    /* background: #ff0000; */
    background: linear-gradient(to right, transparent 0%, #ff0000 50%, transparent 100%);  
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    height: 2px;
    width: 100%;
 }

.contactTitle img{
    height: 80px;
    /* z-index: -1; */
}

.aboutme{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    z-index: 3;
    padding-bottom: 30px;
}

.aboutme p{
    padding: 0px 30px;
    justify-content: center;
    align-items: center;
}

.intro{
    display: flex;
    padding: 0px 300px;
    /* background-image: url("stars.png"); */
    /* background-image: url("mountains.png");
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover; */
    /* opacity: 1px; */
    height: 70vh;
}


.intro img{
    width: 200px;
    border-radius: 20%;
}

.personalJourney{
    display: flex;
    padding: 0px 200px;
}

.slides{
    width: 1100px;
    height: 400px;
    overflow: hidden;
    border: 20px;
    border-radius: 20%;
    box-shadow: 10px 25px 30px rgba(30, 30, 200, 0.3);
}

.slideShow {
    width: 100%;
    display: flex;
    animation: slide 16s infinite;
}

.slideShow img{
    width: 100%;
}

@keyframes slide {
    0%{
        transform: translateX(0);
    }
    25%{
        transform: translateX(0);
    }
    30%{
        transform: translateX(-100%);
    }
    50%{
        transform: translateX(-100%);
    }
    55%{
        transform: translateX(-200%);
    }
    75%{
        transform: translateX(-200%);
    }
    80%{
        transform: translateX(-300%);
    }
    100%{
        transform: translateX(-300%);
    }
}

@media(max-width: 768px){
    .intro{
        padding: 0px 10px;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .personalJourney{
        padding: 0px 10px;
        flex-direction: column;
        overflow-wrap: break-word;
        justify-content: center;
        align-items: center;
    }
    .slides{
        width: 400px;
        height: 550px;
    }
}
