.intro{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0px;
    padding-top: 100px;
    width: 100%;
    height: 100vh;
    position: relative;
}

.particles canvas{
    z-index: -2;
    position: absolute;
}

.introImg{
    height: 250px;
    border-radius: 50%;
    z-index: 1;
    position: relative;
}

@property --angle{
    syntax: "<angle>";
    initial-value: 0deg;
    inherits: false;
}
/* .imgDiv{
    opacity:0;
} */

 .imgDiv::after{
    content: '';
    position: absolute;
    height: 255px;
    width: 300px;
    background-image: conic-gradient(from var(--angle), #4040bf -5.09%, #ff0000 106.28%);
    z-index: -3;
    border-radius: 50%;
    padding: 2px;
    transform: translate(-300px, -2px);
    animation: 3s spin linear infinite;
    opacity: 0;
    transition: 0.5s;
}

.imgDiv:hover::after{
    filter:blur(0.5rem);
    opacity: 1;
    z-index: 0;
}

.info{
    z-index: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.intro h1{
    text-align: center;
    width: 70%;
    font-size: max(4vw, 40px);
    font-weight: 600;
    /* z-index: 1; */
}

.intro h1 span{
    background: linear-gradient(264deg, #4040bf -5.09%, #ff0000 106.28%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    /* z-index: 1; */
}

.intro p{
    font-size: max(1.3vw, 15px);
    /* z-index: 1; */
    align-items: center;
}

.action{
    display: flex;
    align-items: center;
    font-size: max(2vw, 15px);
    font-weight: 500px;
    margin-bottom: 50px;
    gap: 25px;
    z-index: 1;
}

.anchorLink{
    text-decoration: none;
    color: white;
}

.connect{
    padding: 1vw 2vw;
    border-radius: 50px;
    background: linear-gradient(264deg, #4040bf -5.09%, #ff0000 106.28%);
    cursor: pointer;
    transition: 0.5s;
}

.resume{
    padding: 1vw 2vw;
    border-radius: 50px;
    border: 2px solid #FFF;
    cursor: pointer;
    background-color: black;
    /* transition: 0.5s; */
}

.connect:hover{
    /* background:none; */
    border: 2px solid #FFF;
    transform: scale(1.1);
}

.resume:hover{
    border: 2px solid #690650e2;
    /* transform: scale(1.1); */
}

@keyframes spin {
    from{
        --angle: 0deg;
    }
    to{
        --angle: 360deg;
    }
}


@media(max-width: 768px){
    .connect{
        padding: 20px 20px;
    }
    .resume{
        padding: 20px 20px;
    }
    .intro p{
        padding-left: 30px;
    }
}