.navBar{
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    background-color: #000000;
    width: 100vw;
    height: 80px;
    padding: 0px;
    z-index: 5;
}

.navList{
    display: flex;
    align-items: center;
    list-style: none;
    gap: 60px;
    font-size: 15px;
    padding: 0px 50px;
}

.navList li{
    cursor: pointer;
}

.anchorLink{
    text-decoration: none;
    color: white;
}

.anchorLink:hover{
    color:#690650e2;
}

.title{
    font-size: 30px;
    color: #fff;
    font-weight: 600;
    font-family: 'Times New Roman', Times, serif;
    margin-left: 15px;
    /* &:hover{color: #0f0} */
}

.resumeButton{
    padding: 10px 40px;
    border-radius: 50px;
    background: linear-gradient(264deg, #4040bf -5.09%, #ff0000 106.28%);
    cursor: pointer;
    transition: 0.5s;
}

.resumeButton a{
    text-decoration: none;
    color: white;
}

.resumeButton:hover{
    transform: scale(1.05);
}


.selectedMenu {
    padding: 20px 0 8px;
    position: relative;
  }
  
.selectedMenu::before {
    content: "";
    position: absolute;
    /* left: 7%; */
    bottom: 0px;
    left: -13px;
    width: 80px;
    height: 2px;
    background: linear-gradient(to right, transparent 0%, #ff0000 50%, transparent 100%);  
    z-index: -1;
  }

.mobOpen{
    display: none;
    z-index: 1;
}

.mobClose{
    display: none;
    z-index: 1;
}

@media(max-width: 768px){
    .navBar{
        /* margin: 20px 50px; */
        position: absolute;
        justify-content: space-between;
    }
    .resumeButton{
        display: none;
    }
    .mobOpen{
        display: block;
        position: relative;
        right: 30px;
        width: 30px;
    }
    .mobClose{
        display: block;
        position: relative;
        top: 10px;
        /* left: 10%; */
        right: 10%;
        width: 40px; 
        padding-right: 20px;
    }

    .navList{
        position: fixed;
        flex-direction: column;
        align-items: start;
        justify-content: start;
        top: 0;
        gap: 30px;
        background-color: black;
        width: 40%;
        height: 100%;
        z-index: 2;
        transition: 0.5s;
        right: -1000px;
    }
    .navList li{
        font-size: 20px;
        padding-left: 10px;
        flex-direction: row;
        gap: 20px
    }
   
}