.experience{
    display: flex;
    align-items: center;
    margin-top: 50px;
    justify-content: center;
    flex-direction: column;
    /* color: wheat; */
}

.title{
    display: flex;
    align-items: center;
}

.title img{
    height: 5.85vw;
    min-height: 30px;
}

.experienceTitle{
    font-size: max(6.5vw, 30px);
    padding-bottom: 4px;
    position: relative;
    text-decoration: none;
}

.experienceTitle::after {
    /* background: #ff0000; */
    background: linear-gradient(to right, transparent 0%, #ff0000 50%, transparent 100%);  
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    height: 2px;
    width: 100%;
 }

.timeline {
    position: relative;
    width: 85%;
    /* border: 2px solid white; */

}

.timelineBox {
    /* display: flex; */
    position: relative;
    margin: 0px;
    width: 40%;
    left: 5%;
    min-height: 150px;
    /* border: 2px solid white; */
    padding: 0px;
    overflow: scroll;
}

.companyLogo{
    display: flex;
    justify-content: space-between;
}

.companyLogo img{
    height: 70px;
    padding-right: 7px;
}

.companyDetails{
    justify-items: end;
}

.role{
    font-size: max(2vw, 25px);
    font-weight: bold;
    /* color: #e07109; */
    background: linear-gradient(264deg, #4040bf -5.09%, #ff0000 106.28%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-style: italic;
}

.companyName{
    font-size: max(1.2vw, 15px);
    /* color: #690650e2; */
    font-weight: bold;
}

.tenure{
    font-size: max(1.3vw, 10px);
}

.location{
    font-size: max(0.65vw, 10px);
}

.workExp{
    font-size: max(1.3vw, 17px);
    font-weight: bolder;
}

.workExp ul{
    padding-left: 15px;
}

.timelineBox:nth-child(2n) {
    left: 53%;
}

