.timelineDivider {
    position: absolute;
    height: 85%;
    width: 0;
    border: 3px ridge #4040bf;
    /* border-style:ridge; */
    top: 5%;
    left: 50%;
}

.aeroplaneIcon{
    top: 20%;
    position: sticky;
    width: 20px;
    transform: rotate(135deg);
    /* z-index: 1; */
}

.aeroplaneIcon img{
    width: 50px;  
}

@media(max-width: 768px){
    .timeline {
        width: 95%;
    }
}