.educationModule{
        display: flex;
        align-items: center;
        margin-top: 50px;
        justify-content: center;
        flex-direction: column;
        background: linear-gradient(180deg, #111132, #030306);
        padding-bottom: 30px;
        position: relative;
        overflow: hidden;
}

.stars{
        background-image: url("stars.png");
        /* background-attachment: fixed; */
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        width: 100%;
        height: 100%;
        position: absolute;
}

.sun{
    background-image: url("planets.png");
    /* background-attachment: fixed; */
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 100%;
    position: absolute;
}

.title{
    display: flex;
    align-items: center;
}

.title img{
    height: 8.46vw;
}

.educationTitle{
    font-size: max(6.5vw, 30px);
    padding-bottom: 4px;
    position: relative;
    text-decoration: none;
}

.educationTitle::after {
    /* background: #ff0000; */
    background: linear-gradient(to right, transparent 0%, #ff0000 50%, transparent 100%);  
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    height: 2px;
    width: 100%;
 }

.educationBoxes{
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 50%;
    /* border: 2px solid white; */
}

.educationBox{
    display: flex;
    justify-content: center;
    align-items: center;
    /* border: 2px solid white; */
    margin-bottom: 100px;
    padding: 20px;
    position: relative;
    width: 100%;
}

.instituteLogo{
    width: 30%;
    height: 100px;
}

.instituteLogo img{
    width: 100px;
    position: absolute;
}

.instututeInfo{
    justify-content: start;
    height: 100px;
    /* border: 2px solid white; */
    width: 100%;  
}

.instituteName{
    font-size: max(1.95vw, 25px);
    font-weight: bold;
    background: linear-gradient(264deg, #4040bf -5.09%, #ff0000 106.28%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-style: italic;
}

.degree{
    font-size: max(1.5vw, 17px);
    font-weight: bold;
}

.instituteLocation{
    font-size: max(1.3vw, 15px);
}

.completedYear{
    font-size: max(1vw, 12px);
}

@media(max-width: 768px){
    .educationBoxes {
        width: 95%;
    }
}