.contact{
    display: flex;
    align-items: center;
    margin-top: 50px;
    justify-content: center;
    flex-direction: column;
    padding: 20px;
    margin: 20px;
    /* z-index: 1; */
}

.title{
    display: flex;
    align-items: center;
}

.title img{
    height: 6.5vw;
    /* z-index: 1; */
}

.contactTitle{
    font-size: max(6.5vw, 30px);
    padding-bottom: 4px;
    position: relative;
    text-decoration: none;
}

.contactTitle::after {
    /* background: #ff0000; */
    background: linear-gradient(to right, transparent 0%, #ff0000 50%, transparent 100%);  
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    height: 2px;
    width: 100%;
 }

.contactSection{
    display: flex;
    gap: 80px;
    /* z-index: 1; */
}

.contactSection img{
    width: 3vw;
}

.contactDetail{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.contactDetail p{
    font-size: 2vw;
}

@media(max-width: 768px){
    .contactSection{
        gap: 10px
    }
}